import React from "react";
import { COLORS, TEXT_STYLES } from "../../constants";
import bgImg from "@/public/assets/imgs/Onboarding Page.jpg";
import Image from "next/image";

const Page = () => {
  return (
    <div style={{ position: "relative", width: "100%", height: "100vh" }}>
      {/* Background Image */}
      <Image src={bgImg} alt="Onboarding" fill style={{ objectFit: "cover" }} />

      {/* Gradient Overlay */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          background: "linear-gradient(180deg, rgba(0,0,0,0) 0%, #000 100%)",
        }}
      />

      <div
        style={{
          ...TEXT_STYLES.READEX_PRO.BOLD.H1,
          color: COLORS.WHITE,
          position: "relative",
          zIndex: 1,
        }}
        className="flex items-end content-center flex-col text-right"
      >
        <div>
          لنستكشف انواع جديدة من القهوة
          <div style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H5 }}>
            انواع مختلفة من القهوة الطازجة يوميا بنكهات مختلفة
          </div>
        </div>
      </div>
      <Button isMain text={"Sfds"} onClick={() => console.log("HI")} />
    </div>
  );
};

export default Page;
